@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,700;1,700&display=swap);
.global-space{padding-bottom:2em}*,*:before,*:after{outline:none;box-sizing:inherit;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{font-family:"Open Sans",sans-serif;font-weight:400;font-size:16px;color:#3b454e;line-height:1.625}img{max-width:100%;height:auto}label{font-size:14px;color:#555d66}a{color:#0073aa}h1,h2,h3,h4,h5,h6{font-weight:700}.btn-primary{color:#fff;background-color:#0073aa;border-color:#0073aa}.btn-primary:hover,.btn-primary:focus{background-color:#0073aa !important;border-color:transparent}.btn-primary:focus{box-shadow:0 0 0 1px #fff,0 0 0 3px #0073aa}.form-group{margin:18px 10px 10px}.form-control:focus{border-color:#0073aa;box-shadow:0 0 0 1px #0073aa;outline:2px solid #0000}.nav-tabs .nav-link{border:none;padding:24px 40px;color:inherit}.nav-tabs .nav-link.active{font-weight:700;border-color:transparent;box-shadow:inset 0 -3px #0073aa}.navbar{background-color:#23282d;padding:23px}.navbar .header .navbar-brand{width:23%;margin-right:50px}.navbar .header .navbar-nav .nav-link{color:var(--light);padding-left:15px;padding-right:15px}.jumbotron{background-color:#0073aa;color:var(--light)}.gb__info-headline,.gb__info-content{text-align:center}.gb__info-content{font-size:24px;max-width:780px;margin:30px auto 0}.gb__generator{padding-top:30px;padding-bottom:30px}.gb__generator-tab{padding:43px 10px}.gb__generator-tab h5{margin-left:10px}.gb__generator-buttons{justify-content:center}.gb__generator-button{width:29%;margin-right:12px;height:48px}.gb__generator-button.btn-outline-primary{margin-top:0;color:#0073aa;border-color:#0073aa}.gb__generator-button.btn-outline-primary:hover,.gb__generator-button.btn-outline-primary:focus{background:#f1f1f1 !important}#wporg-footer{margin:0 auto;padding:22px 14px 65px 14px;border-top:1px solid #dfdfdf;background-color:#f7f7f7}#wporg-footer .container{display:flex}#wporg-footer ul{width:135px;margin-left:24px;margin-bottom:20px;padding-left:0}#wporg-footer ul li{font-size:14px;margin-bottom:1px;color:#bbb;list-style-type:none}#wporg-footer ul li a{color:#21759b;text-decoration:none;font-weight:normal}#wporg-footer .cip-image{width:-webkit-max-content;width:-moz-max-content;width:max-content;margin:64px auto 0px}#wporg-footer .copyright{text-align:center;margin:20px 10px;font-size:13px;color:#8e8e8e}

